@import url("https://fonts.cdnfonts.com/css/tt-hoves-pro-trial?styles=143134,143123,143140");
:root {
  --font-family: "TT Hoves Pro Trial", sans-serif;
  --content-width: 1034px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --font-l: 300;
  --font-r: 400;
  --font-b: 700;
  --h-tr: all 0.5s ease;
  --d-tr: all 0.3s ease;
}

.project-box__img--col2 img:hover, .project-box__img--col1 img:hover {
  box-shadow: 0px 5px 35px rgba(225, 242, 255, 0.9);
}

.contacts-box__form--send a svg, .contacts-box__form--send, .project-box__btn a svg, .project-box__btn, .project-box__img--col2 img, .project-box__img--col1 img, .headercont-slider--commit__link a img, .navigation-menu ul li:after, .contacts-box__form--send a img, .project-box__btn a img, .about-text__btn a img {
  transition: var(--d-tr);
}

.contacts-box__form--send:hover svg, .contacts-box__form--send:hover a, .contacts-box__form--send:hover, .project-box__btn:hover svg, .project-box__btn:hover a, .project-box__btn:hover, .project-box__img--col2 img:hover, .project-box__img--col1 img:hover, .headercont-slider--commit__link:hover img, .navigation-menu ul li:hover:after, .contacts-box__form--send:hover img, .project-box__btn:hover img, .about-text__btn:hover img {
  transition: var(--h-tr);
}

.contacts-box__form--send, .project-box__btn, .about-text__btn {
  width: 13.875rem;
  cursor: pointer;
  background: #fff;
  bottom: 0;
  padding: 1.875rem 3.125rem;
}
.contacts-box__form--send:hover img, .project-box__btn:hover img, .about-text__btn:hover img {
  padding-left: 1rem;
}
.contacts-box__form--send a, .project-box__btn a, .about-text__btn a {
  text-transform: uppercase;
  color: #333333;
}
.contacts-box__form--send a img, .project-box__btn a img, .about-text__btn a img {
  padding-left: 0.75rem;
}

.box-container {
  width: 925px;
  float: right;
  padding: 0 15px;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
html {
  box-sizing: border-box;
}

ul,
p,
h3,
h2 {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  font-weight: var(--font-r);
  font-size: 0.75rem;
  line-height: 1rem;
  color: #333333;
  background: #fff;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: #fff;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: var(--container-width);
}

.navigation {
  padding: 1.813rem 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.navigation-menu {
  margin-left: 20.625rem;
}
.navigation-menu ul {
  display: flex;
  align-items: center;
  gap: 3.75rem;
}
.navigation-menu ul li {
  cursor: pointer;
  position: relative;
}
.navigation-menu ul li:after {
  content: "";
  position: absolute;
  border-top: 0.063rem solid #333333;
  border-bottom: 0.063rem solid #333333;
  width: 0;
  top: 0;
  bottom: 0;
  left: 0;
}
.navigation-menu ul li:hover:after {
  width: 100%;
}
.navigation a {
  font-size: 0.75rem;
  line-height: 1rem;
  color: #333333;
  text-transform: uppercase;
  font-weight: var(--font-r);
}

.headercont {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.headercont-info {
  text-transform: uppercase;
  margin-right: 7.625rem;
}
.headercont-info h1 {
  color: #BDBDBD;
  font-size: 4rem;
  line-height: 4.375rem;
  font-weight: var(--font-l);
}
.headercont-info p {
  font-weight: var(--font-b);
  color: #333333;
}
.headercont-info__btn button + button {
  margin-left: 1.438rem;
}
.headercont-info__btn button {
  background: transparent;
  width: 3.313rem;
  height: 3.313rem;
  border: 1px solid #BDBDBD;
}
.headercont-slider {
  overflow: hidden;
  width: 100%;
}
.headercont-slider--commit li {
  position: relative;
}
.headercont-slider--commit__link {
  cursor: pointer;
  position: absolute;
  background: #fff;
  bottom: 0;
  padding: 1.875rem 3.125rem;
}
.headercont-slider--commit__link:hover img {
  padding-left: 1rem;
}
.headercont-slider--commit__link a {
  text-transform: uppercase;
  color: #333333;
}
.headercont-slider--commit__link a img {
  padding-left: 0.75rem;
}

.swiper-pagination-fraction {
  font-size: 1.5rem;
  line-height: 1.875rem;
  color: #BDBDBD;
  font-weight: var(--font-r);
  padding-top: 5.625rem;
}

.swiper-pagination-current {
  padding-right: 1.563rem;
}

.swiper-pagination-total {
  padding-left: 1.563rem;
}

.about {
  margin-top: 7.5rem;
}
.about-inner {
  position: relative;
  z-index: 99999;
  padding-top: 1.875rem;
  display: flex;
  justify-content: center;
  gap: 1.875rem;
}
.about-inner:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 27.188rem;
  background: #FBFBFB;
  top: 0;
  z-index: -1;
}
.about-img {
  display: flex;
  align-items: center;
  gap: 1.875rem;
  height: 27.188rem;
}
.about-img__first {
  width: 16.875rem;
}
.about-img__first img {
  padding-top: 1.875rem;
}
.about-text {
  width: 25rem;
}
.about-text h2 {
  font-size: 4rem;
  line-height: 4.375rem;
  font-weight: var(--font-l);
  color: #BDBDBD;
  margin-bottom: 1.25rem;
}
.about-text p {
  font-size: 1rem;
  line-height: 1.25rem;
}
.about-text__btn {
  margin-top: 1.25rem;
}
.about-text__btn a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.tasks {
  margin: 7.5rem 0;
}
.tasks h2 {
  font-size: 4rem;
  line-height: 4.375rem;
  font-weight: var(--font-l);
  color: #BDBDBD;
  margin-bottom: 3.75rem;
}
.tasks ul {
  display: flex;
  justify-content: space-between;
  gap: 11.688rem;
}
.tasks ul li {
  width: 35.625rem;
  display: flex;
  align-items: center;
  gap: 1.875rem;
}
.tasks ul li h3 {
  font-size: 12.5rem;
  line-height: 13.75rem;
  color: #BDBDBD;
}
.tasks ul li p {
  font-size: 1.188rem;
  line-height: 1.438rem;
  color: #333333;
  font-weight: var(--font-r);
}

.project h2 {
  font-size: 4rem;
  font-weight: var(--font-l);
  line-height: 4.375rem;
  color: #BDBDBD;
  margin-bottom: 3.75rem;
}
.project-box {
  margin-bottom: 0.625rem;
}
.project-box__img--col1, .project-box__img--col2 {
  display: flex;
  margin: 0 auto;
}
.project-box__img--col1 {
  margin-bottom: 30px;
}
.project-box__img--col1, .project-box__img--col2 {
  gap: 30px;
}
.project-box__btn {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-left: auto;
  background: #333333;
}
.project-box__btn:hover {
  background: #fff;
}
.project-box__btn:hover a {
  color: #333333;
}
.project-box__btn:hover svg {
  margin-left: 1rem;
}
.project-box__btn:hover svg path {
  fill: #333333;
}
.project-box__btn a {
  color: #fff;
}
.project-box__btn a svg {
  margin-left: 0.75rem;
}
.project-box__btn a svg path {
  fill: #fff;
}

.contacts {
  margin: 7.5rem 0;
}
.contacts h2 {
  font-size: 4rem;
  line-height: 4.375rem;
  font-weight: var(--font-l);
  color: #BDBDBD;
  margin-bottom: 1.375rem;
}
.contacts-box {
  display: flex;
  gap: 1.875rem;
}
.contacts-box__form {
  display: flex;
  flex-direction: column;
  width: 24.438rem;
}
.contacts-box__form--input {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}
.contacts-box__form--input input {
  border: none;
  outline: none;
  background: #F3F3F3;
  height: 2.875rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding: 0.938rem 0 0.938rem 1.313rem;
}
.contacts-box__form textarea {
  margin: 0.625rem 0 1.313rem;
  border: none;
  outline: none;
  background: #F3F3F3;
  height: 9.188rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding: 0.938rem 0 0.938rem 1.313rem;
}
.contacts-box__form--confirm {
  display: flex;
  margin-left: 1.313rem;
}
.contacts-box__form--confirm svg {
  width: 1.688rem;
  height: 1.688rem;
}
.contacts-box__form--confirm p {
  font-size: 0.875rem;
  font-weight: var(--font-r);
  margin-left: 0.313rem;
}
.contacts-box__form--send {
  margin-top: 1.313rem;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  background: #333333;
}
.contacts-box__form--send:hover {
  background: #fff;
}
.contacts-box__form--send:hover a {
  color: #333333;
}
.contacts-box__form--send:hover svg {
  margin-left: 1rem;
}
.contacts-box__form--send:hover svg path {
  fill: #333333;
}
.contacts-box__form--send a {
  color: #fff;
}
.contacts-box__form--send a svg {
  margin-left: 0.75rem;
}
.contacts-box__form--send a svg path {
  fill: #fff;
}
.contacts-box__img img {
  height: 23.063rem;
}